<template>
	<div id="editPwd">
		<tipsDialog :msg="msgTips"></tipsDialog>
		<topbar title=""></topbar>
		<back></back>
		<cs title=""></cs>
		<div class="page_cont">
			<div class="ipt_row">
				<label for="phone"></label>
				<input type="number" id="phone" placeholder="" disabled v-model="userInfo.phone" />
			</div>
			<div class="ipt_row">
				<label for="code"></label>
				<input type="number" id="code" placeholder="" v-model.trim="code" />
				<span class="code" @tap="getCode">{{volid ? info : (time + '')}}</span>
			</div>
			<div class="ipt_row">
				<label for="pwd"></label>
				<input type="password" id="pwd" placeholder="" v-model.trim="pwd" />
			</div>
			<div class="ipt_row">
				<label for="newPwd"></label>
				<input type="password" id="newPwd" placeholder="" v-model.trim="newPwd" />
			</div>
			<btn name="" @tap.native="confirmEdit"></btn>
		</div>
	</div>
</template>

<script>
	import topbar from '../../components/Topbar.vue'
	import back from '../../components/back.vue'
	import cs from '../../components/customerService.vue'
	import btn from '../../components/bigBtn.vue'
	import tipsDialog from '../../components/tipsDialog.vue'
	export default{
		name:'editPwdFirst',
		components: {topbar, back, cs, btn, tipsDialog},
		computed: {
			msgTips: function(){
				return this.msg;
			},
			PATH: function(){
				return this.$store.getters.PATH;
			},
			volid: function(){
				if(this.time <= 0){
					return true
				}else{
					return false
				}
			},
			userInfo: function(){
				return this.$store.state.account;
			}
		},
		data(){
			return {
				msg: '',
				code: '',
				time: 0,
				info:'',
				pwd: '',
				newPwd: '',
				pwdReg: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,18}$/,
				phoneReg: /^(((13[0-9])|(14[5-7])|(15[0-9])|(17[0-9])|(18[0-9]))+\d{8})$/
			}
		},
		methods: {
			getCode: function(e){
				if($(e.target).hasClass('current')) return false;
				//
				$(e.target).addClass('current');
				this.time = 60;
				var timing = setInterval(function(){
					this.time--;
					if(this.time <= 0){
						clearInterval(timing);
						$(e.target).removeClass('current');
					}
				}.bind(this), 1000);
				//
				this.$http.post(this.PATH + '/user/security/send_sms',{emulateJSON: true},{
					headers: {
						'token':  this.userInfo.token,
						'secret': this.userInfo.secret
					},
					params: {
						mobile: this.userInfo.phone,
						type: 1
					},
					timeout: 5000
				}).then(function(e){
					var data = e.body;
					if(data.success == true){
						if(data.code == 1){
							this.$children[0].isShow = true;
							this.msg = '';
						}
					}else{
						switch (data.code){
							case '2':
								this.$children[0].isShow = true;
								this.msg = '';
								break;
							case '4':
								this.$children[0].isShow = true;
								this.msg = '';
								break;
							case '5':
								this.$children[0].isShow = true;
								this.msg = '';
								break;
							case '6':
								this.$children[0].isShow = true;
								this.msg = '';
								break;
							default:
								break;
						}
					}
				}.bind(this), function(){
					this.$children[0].isShow = true;
					this.msg = ''
				});
			},
			confirmEdit: function(){
				if(this.code == ''){
					this.$children[0].isShow = true;
					this.msg = '';
				}else if(this.pwd == ''){
					this.$children[0].isShow = true;
					this.msg = '';
				}else if(this.newPwd == ''){
					this.$children[0].isShow = true;
					this.msg = '';
				}else if(this.newPwd == this.userInfo.password){
					this.$children[0].isShow = true;
					this.msg = '';
				}else if(this.pwdReg.test(this.pwd) == false || this.pwdReg.test(this.newPwd) == false){
					this.$children[0].isShow = true;
					this.msg = '';
				}else if(this.pwd != this.newPwd){
					this.$children[0].isShow = true;
					this.msg = '';
				}else{
					//
					this.$http.post(this.PATH + '/user/security/update_loginPwd', {emulateJSON: true},{
						headers: {
							'token':  this.userInfo.token,
							'secret': this.userInfo.secret
						},
						params: {
							code: this.code,
							password: this.pwd,
						},
						timeout: 5000
					}).then(function(e){
						var data = e.body;
						if(data.success == true){
							if(data.code == 1){
								this.$children[0].isShow = true;
								this.msg = '';
								this.$store.state.account.islogin = false;
								localStorage.removeItem("user");
								this.time = 0;
								this.code = '';
								this.pwd = '';
								this.newPwd = '';
								setTimeout(function(){
									this.$router.replace({path: '/login', query: {isJump: 1}});
								}.bind(this), 1000);
							}
						}else{
							switch (data.code){
								case '-2':
									this.$children[0].isShow = true;
									this.msg = '';
									break;
								case '-1':
									this.$children[0].isShow = true;
									this.msg = '';
									break;
								case '2':
									this.$children[0].isShow = true;
									this.msg = '';
									break;
								case '3':
									this.$children[0].isShow = true;
									this.msg = '';
									break;
								case '4':
									this.$children[0].isShow = true;
									this.msg = '';
									break;
								case '5':
									this.$children[0].isShow = true;
									this.msg = '';
									break;
								case '6':
									this.$children[0].isShow = true;
									this.msg = '';
									break;
								case '7':
									this.$children[0].isShow = true;
									this.msg = '';
									break;
								default:
									break;
							}
						}
					}.bind(this), function(){
						this.$children[0].isShow = true;
						this.msg = '';
					});
				}
			}
		},
		mounted: function(){
			$("#editPwd").css("height", window.screen.height - 20 + 'px');
		},
		activated: function(){}
	}
</script>

<style scoped lang="less">
@import url("../../assets/css/base.less");
/*ip6p*/
@media (min-width:411px) {
    #editPwd{
    	width: 100%;
    	overflow: hidden;
    	padding-top: 65px;
    	background: @deepblue;
    }
}
/*ip6*/
@media (min-width:371px) and (max-width:410px) {
	#editPwd{
    	width: 100%;
    	overflow: hidden;
    	padding-top: 65px*@ip6;
    	background: @deepblue;
    }
}
/*ip5*/
@media(max-width:370px) {
	#editPwd{
    	width: 100%;
    	overflow: hidden;
    	padding-top: 65px*@ip5;
    	background: @deepblue;
    }
}
</style>


