<template>
	<div id="chartfens">
		<div id="fens" style="margin: 0 auto;">

		</div>
		<div id="volume" style="margin: 0 auto;">

		</div>
	</div>
</template>

<script>
	import { mapMutations,mapActions } from 'vuex'
	export default {
		name: 'chartfens',
		methods: {
			...mapMutations([
				'drawfens',
				'setfensoption',
				'setfensoptionsecond',
				'updateTempdata'
			]),
		},
		data() {
			return {
				obj: {
					id1: 'fens',
					id2: 'volume',
					CommodityNo:''
				},
			}
		},
		computed:{
//			Data(){
//				return this.$store.state.market.jsonData.Parameters.Data;
//			},
			jsonData(){
				return this.$store.state.market.jsonData
			}
		},
		watch: {
//			Data: function(n, o) {
//				this.setfensoption();
//				//store
////				this.$store.commit('setfensoption');   
////				this.$store.commit('drawfenssecond',this.obj);
//				this.drawfenssecond(this.obj);
//			},
//			jsonData(n,o){
////				this.setfensoption();
////				this.drawfenssecond(this.obj);
//				console.log(n);
//				this.$store.state.jsonDatatemp=n;
//				this.setfensoptionsecond();
//				this.drawfenssecond(this.obj);
//			}
		},
		mounted: function() {
			this.CommodityNo=this.$parent.EngName.substring(0,this.$parent.EngName.length-4);
//			console.log(this.CommodityNo);
			
			if(this.$store.state.isshow.isfensshow==false){
				this.setfensoption();
				this.drawfens(this.obj);
			}else{
				this.updateTempdata(this.CommodityNo);
				this.setfensoptionsecond();
				this.drawfens(this.obj);
			}
		}
	}
</script>

<style scoped lang="less">
	@import url("../assets/css/main.less");
	/*ip5*/
	
	@media(max-width:370px) {
		#chartfens {
			width: 100%;
			height: 390px*@ip5;
		}
		#fens {
			width: 100%;
			height: 390px/5*3*@ip5;
		}
		#volume {
			width: 100%;
			height: 390px/5*2*@ip5;
		}
	}
	/*ip6*/
	
	@media (min-width:371px) and (max-width:410px) {
		#chartfens {
			width: 100%;
			height: 390px*@ip6;
		}
		#fens {
			width: 100%;
			height: 390px/5*3*@ip6;
		}
		#volume {
			width: 100%;
			height: 390px/5*2*@ip6;
		}
	}
	/*ip6p*/
	
	@media (min-width:411px) {
		#chartfens {
			width: 100%;
			height: 390px;
		}
		#fens {
			width: 100%;
			height: 390px/5*3*@ip6p;
		}
		#volume {
			width: 100%;
			height: 390px/5*2*@ip6p;
		}
	}
</style>


